import React from "react";
import styled from "styled-components";
import ReactPlayer from 'react-player';

const RcInfo = () => {
  return (
    <Wrap>
      <ContentWrap>
        <div className="notoBold sorry-text">.</div>
        <span className="notoBold text-wrap">
          <span style={{ color: "#FE4A00" }}>' '</span>
          <span style={{ fontWeight: "1000" }}>   </span>
          <br />
            .
        </span>
        <ReactPlayer url='https://www.youtube.com/watch?v=POgtd-SID48' playing={false} controls width='61.25rem' height='34.5rem'/>
        <div className="notoBold quote">  <br/>     .</div>
        <Box>
          <div className="text">
            <div className="notoBold red-text">
            01. ,    ?
            </div>
            <div className="notoRegular black-text">
            ,      IT     .       ?        
       .
            </div>
          </div>
        </Box>
        <Box style={{marginTop:"2.0625rem", marginBottom:"12.0625rem"}}>
          <div className="text">
            <div className="notoBold red-text">
            02.     <br/>
            " .
            </div>
            <div className="notoRegular black-text">
                <span style={{fontWeight:"bold"}}> ,   ,    </span>   . 
             <span style={{fontWeight:"bold"}}>    ,  </span>   .
            </div>
          </div>
        </Box>
      </ContentWrap>
    </Wrap>
  );
};

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ContentWrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 61.25rem;

  .sorry-text {
    margin-top: 4.6875rem;
    font-size: 4rem;
    color: #fe4a00;
  }

  .text-wrap {
    margin: 0.25rem 0 2.8125rem 0;
    font-size: 2rem;
    text-align: center;
  }

  .left-quote{
    margin-top: 6.25rem;
    font-size: 7.875rem;
    color: #FE4A00;
  }

  .quote{
    font-size: 1.875rem;
    color: #FE4A00;
    margin-right: auto;
    margin-bottom: 7.8125rem;
  }
`;

const Box = styled.div`
  width: 61.25rem;
  width: 100%;
  background-color: #ffffff;
  box-shadow: 1.125rem 1.5rem 3.75rem rgba(115, 115, 115, 0.1);
  border-radius: 1.25rem 1.25rem 1.25rem 0;

  .text{
    display: flex;
    width: 55.625rem;
    flex-direction: column;
    margin: 3.3125rem auto;
    text-align: left;
  }

  .red-text{
    font-size: 1.625rem;
    color: #fe4a00;
    margin-bottom: 1.125rem;
  }

  .black-text{
    font-size: 1.25rem;
    color: #000000;
  }
`;

export default RcInfo;
